<template>
  <div class="container">
    <header>
      <h3>Personeller</h3>
    </header>

    <div class="table-wrapper">
      <table class="table-users">
        <tr>
          <th style="width: 50%">Personel İsmi</th>
          <th style="width: 50%">Personel Durumu</th>
        </tr>
        <tr v-for="personel in PersonellerDizi" :key="personel.key">
          <td>{{ personel.personel_isim }}</td>
          <td>{{ personel.personel_durum }}</td>
 
        </tr>
      </table>
    </div>
  </div>

</template>

<script>
import { db } from "../firebaseDb";

export default {
  data() {
    return {
      PersonellerDizi: [],
    };
  },
  created() {

    
db.collection("personeller").orderBy("sira", "asc").onSnapshot((snapshotChange) => {
      this.PersonellerDizi = [];
      snapshotChange.forEach((doc) => {
        this.PersonellerDizi.push({
          key: doc.id,
          personel_isim: doc.data().personel_isim,
          personel_durum: doc.data().personel_durum,
        });
      });
    });
  },
};
</script>

